import React, { Component } from 'react'
import md5 from 'md5'
import Member from './Member'

class TestList extends Component {
  constructor() {
    super()
    this.state = {
      listMembers: '',
      singleMember: '',
      memhash: '',
      totalItems: 0
    }
  }

  // addMember = () => {

  // }

  // returns a JSON object with a key of "members" and
  // value of an array of objects
  getMembers = () => {
    fetch("/api/memberList")
    .then((res) => {
      return res.json()
    })
    .then( (json) => {
      this.setState({
        listMembers: json.members,
        totalItems: json.total_items
      })
      // console.log("Members:", this.state.listMembers)
    })
    .catch((err) => {
      console.log("error", err)
    })
  }

  // returns a single JSON object representing the member
  // referenced by memhash
  getOneMember = () => {
    console.log("Getting member")
    this.setState({
      memhash: md5('davidday@waitrose.com')
    })
    fetch(`/api/getMember/${this.state.memhash}`)
    .then((res) => {
      return res.json()
    })
    .then( (json) => {
      this.setState({
        singleMember: JSON.stringify(json)
      })
      console.log("Member:", this.state.singleMember)
    })
    .catch((err) => {
      console.log("error", err)
    })
  }

  render() {
    let members = this.state.listMembers
    if(this.state.listMembers){
      return (
        <div>
          <h1>OJF Members</h1>
          <h5>{`Total membership: ${this.state.totalItems}`}</h5>
          <table className="memList">
            {
              members.map((member, index) =>
                <tr className="memLine" key={index}>
                <td className="memCol">
                  {`${member.merge_fields.FNAME} 
                  ${member.merge_fields.LNAME}: `}
                </td>
                <td className="memCol">
                  {`${member.email_address}`}   
                </td>
                <td className="memCol">
                  {`${member.status}`}
                </td>
                </tr>
              )
            }
          </table>
          <br />
        </div>
      )
    } else if (this.state.singleMember) {
      let chosenMember = JSON.parse(this.state.singleMember)
      return (
         <div>
           <h1>Member Details</h1>
           <Member />
           <table className="memList">
              <tr className="memLine">
                <td className="memCol">
                  {`${chosenMember.merge_fields.FNAME} 
                  ${chosenMember.merge_fields.LNAME}: `}
                </td>
                <td className="memCol">
                  {`${chosenMember.email_address}`}   
                </td>
                <td className="memCol">
                  {`${chosenMember.status}`}
                </td>
              </tr>
            </table>
         </div>
       )
    } 
  
    return (
      <div>
        <h1>OJF Members</h1>
        <button onClick={this.getMembers}>List All Members</button>
        <br /><br />
        <button onClick={this.getOneMember}>Get test member</button>
      </div>
    )
  }
}

export default TestList
